import styles from "./IntroductionText.module.scss";
import githubIcon from "../../../Images/Introduction/VisualElements/githubIcon.svg";
import linkedinIcon from "../../../Images/Introduction/VisualElements/linkedinIcon.svg";

function IntroductionText() {
  const openGithub = () => {
    window.open("https://github.com/AllanBOMelo");
  };

  const openLinkedin = () => {
    window.open("https://www.linkedin.com/in/allan-barbosa-65110219a/");
  };

  function aboutmeTarget() {
    const container = document.getElementById("aboutContainer");
    container.scrollIntoView({ behavior: "smooth", block: "center" });
  }

  return (
    <section className={styles.introductionTextContainer}>
      <h4 className={styles.text}>Ol, meu nome  Allan.</h4>
      <h1 className={styles.highlightedText}>
        Eu sou um <br /> Desenvolvedor
      </h1>
      <h4 className={styles.text}>E este  o meu portflio!</h4>

      <div className={styles.iconsContainer}>
        <img
          onClick={openGithub}
          className={styles.icon}
          src={githubIcon}
          alt="Icon"
        />
        <img
          onClick={openLinkedin}
          className={styles.icon}
          src={linkedinIcon}
          alt="Icon"
        />
      </div>

      <button className={styles.aboutMeButton} onClick={aboutmeTarget}>
        Sobre mim
      </button>
    </section>
  );
}

export default IntroductionText;
