import styles from "./AboutImage.module.scss";
import aboutImage from "../../../Images/About/VisualElements/astronaut on moon.png";
import profileImage from '../../../Images/About/VisualElements/profile.jpg';
import { useEffect } from "react";

function AboutImage() {
  const image = new Image();

  useEffect(() => {
    const container = document.getElementById("imageContainer");
    container.innerHTML = `<img src="${image.src}" alt='aboutImage' class="${styles.Image}" />`;
  });

  image.src = profileImage;

  return (
    <aside className={styles.aboutImageContainer}>
      <div id="imageContainer"></div>
    </aside>
  );
}

export default AboutImage;
