import styles from "./DesktopNavBar.module.scss";
import logo from "../../../Images/Introduction/VisualElements/moon.svg";

function DesktopNavBar() {
  function scrollPage(e) {
    let option = e.target.id;

    if (option === "aboutOption") {
      const container = document.getElementById("aboutContainer");
      container.scrollIntoView({ behavior: "smooth", block: "center" });
    }

    if (option === "skillsOption") {
      const container = document.getElementById("skillsContainer");
      container.scrollIntoView({ behavior: "smooth", block: "center" });
    }

    if (option === "projectsOption") {
      const container = document.getElementById("projectsContainer");
      container.scrollIntoView({ behavior: "smooth", block: "center" });
    }

    if (option === "contactOption") {
      const container = document.getElementById("contactContainer");
      container.scrollIntoView({ behavior: "smooth", block: "center" });
    }
  }
  return (
    <header className={styles.desktopNavBarContainer}>
      <div className={styles.mainLogoContainer}>
        <img src={logo} alt="Logo" className={styles.mainLogo} />
      </div>

      <nav className={styles.navBar}>
        <p id="aboutOption" onClick={scrollPage} className={styles.navOption}>
          Sobre
        </p>
        <p id="skillsOption" onClick={scrollPage} className={styles.navOption}>
          Habilidades
        </p>
        <p
          id="projectsOption"
          onClick={scrollPage}
          className={styles.navOption}
        >
          Projetos
        </p>
        <p id="contactOption" onClick={scrollPage} className={styles.navOption}>
          Contato
        </p>
      </nav>
    </header>
  );
}

export default DesktopNavBar;
