import styles from "./MobileNavBar.module.scss";
import logo from "../../../Images/Introduction/VisualElements/moon.svg";
import classNames from "classnames";

function MobileNavBar() {
  let windowHeight = window.screen.height;

  window.onscroll = () => {
    if (
      document.body.scrolltop > windowHeight ||
      document.documentElement.scrollTop > windowHeight
    ) {
      let fixedNavBar = document.getElementById(
        "mobileNavBarContainerAfterScroll"
      );

      fixedNavBar.style.opacity = "1";
      document.getElementById("mobileMenuButton").checked = false;
    } else {
      let fixedNavBar = document.getElementById(
        "mobileNavBarContainerAfterScroll"
      );

      fixedNavBar.style.opacity = "0";
      document.getElementById("mobileMenuButtonScrolled").checked = false;
    }
  };

  function scrollPage(e) {
    let option = e.target.id;

    if (option === "aboutMobileNav") {
      const container = document.getElementById("aboutContainer");
      container.scrollIntoView({ behavior: "smooth", block: "center" });
    }

    if (option === "skillsMobileNav") {
      const container = document.getElementById("skillsContainer");
      container.scrollIntoView({ behavior: "smooth", block: "center" });
    }

    if (option === "projectsMobileNav") {
      const container = document.getElementById("projectsContainer");
      container.scrollIntoView({ behavior: "smooth", block: "center" });
    }

    if (option === "contactMobileNav") {
      const container = document.getElementById("contactContainer");
      container.scrollIntoView({ behavior: "smooth", block: "center" });
    }
  }

  return (
    <>
      <header className={styles.mobileNavBarContainer}>
        <img src={logo} alt="logoImage" className={styles.logoImage} />

        <label className={styles.menuButton}>
          <input type="checkbox" id="mobileMenuButton" />
          <span></span>
          <span></span>
          <span></span>

          <div className={styles.menuContainer}>
            <p id="aboutMobileNav" onClick={scrollPage}>
              Sobre
            </p>
            <p id="skillsMobileNav" onClick={scrollPage}>
              Habilidades
            </p>
            <p id="projectsMobileNav" onClick={scrollPage}>
              Projetos
            </p>
            <p id="contactMobileNav" onClick={scrollPage}>
              Contato
            </p>
          </div>
        </label>
      </header>

      <header
        id="mobileNavBarContainerAfterScroll"
        className={styles.mobileNavBarContainerAfterScroll}
      >
        <label
          className={classNames(styles.menuButton, styles.afterScrollButton)}
        >
          <input type="checkbox" id="mobileMenuButtonScrolled" />
          <span></span>
          <span></span>
          <span></span>

          <div className={styles.menuContainer}>
            <p id="aboutMobileNav" onClick={scrollPage}>
              Sobre
            </p>
            <p id="skillsMobileNav" onClick={scrollPage}>
              Habilidades
            </p>
            <p id="projectsMobileNav" onClick={scrollPage}>
              Projetos
            </p>
            <p id="contactMobileNav" onClick={scrollPage}>
              Contato
            </p>
          </div>
        </label>
      </header>
    </>
  );
}

export default MobileNavBar;
